import React, {useEffect, useState} from "react";
import Mobile from './Mobile';
import Desktop from './Desktop';
import './index.css';
import {connect} from "react-redux";
import {appActions} from "../../redux/actions";

const Foods = ({foods, setCartItem}) => {
    const [modal, setModal] = useState(null);
    const [food, setFood] = useState(null);
    const addToCart = (foodItem, foodPropertyId) => {
        if (foodItem.options.length > 0) {
            setFood(foodItem)
            modal.open();
        } else {
            setCartItem(foodPropertyId);
        }
    };

    useEffect(() => {
        var modals = document.querySelectorAll('.modal');
        setModal(window.M.Modal.init(modals)[0])

    }, [])

    return (
        <div className="content">
            {(/Android|webOS|iPhone|iPad|iPod|BlackBerry/i.test(navigator.userAgent))
                ? <Mobile foods={foods} selectFood={addToCart}/>
                : <Desktop foods={foods} selectFood={addToCart}/>
            }

            {
                <div id="modal1" className="modal modal-fixed-footer">

                    <div className="modal-content">
                        <h3 className={"header"}>Выберите опции</h3>

                        {food && food.options.map((foodOption, key) => {
                            return (
                                <div className={"ModalOptionsGroup_root"} key={"foodOption-" + key}>
                                    <div className={"ModalOptionsGroup_topLineWrapper"}>
                                        <div className={"ModalOptionsGroup_topLine"}>
                                            <div className={"ModalOptionsGroup_name"}><span>{foodOption.categoryName}</span></div>
                                        </div>
                                    </div>

                                    <div className={"options"}>
                                        {foodOption.items.map(item => {
                                            return (
                                                <label key={"option-" + item.id}>
                                                    <input type="checkbox" />
                                                    <span>{item.name}</span>
                                                </label>
                                            )
                                        })}
                                    </div>

                                </div>
                            )
                        })}

                    </div>
                    <div className="modal-footer">
                        <a href="#!" className="modal-close waves-effect waves-green btn-flat">Agree</a>
                    </div>
                </div>
            }

        </div>
    )
}

const mapStateToProps = state => {
    return {
        foods: state.foodReducer.items
    }
}

export default connect(mapStateToProps, {...appActions})(Foods);